import { mapMutations,mapGetters } from 'vuex';
import kong from '@/static/mbcImg/my/kong.png';
export default {
	data() {
		return {
			list:[],
			kong:kong,
			pages:'1'
		}
	},
	created() {
		// 消息产品列表
		this.product();
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d ;
		}
	},
	methods: {
		loadMore:function(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/msg/selectList?userId='+landRegistLG.user.id+'&page='+this.pages+'&instCustType=0', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						if(response.data.content.list==0){
							uni.showToast({
								title: '没有数据啦',
								icon: 'none',
								duration: 1000
							});
						}else if(response.data.content.list!==0){
							this.pages++;
							response.data.content.list.map((item,index)=>{
								this.list.push(item)
							})
						}
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		product(){
			if (uni.getStorageSync('landRegist')) {
